import React from 'react'
import styled from 'styled-components'
import { Button } from '@pancakeswap-libs/uikit'
import { purchasePay } from 'utils/callHelpers'

const Action = styled.div`
  padding-top: 16px;
`

interface FarmCardActionsProps {
  contract?: any
  account?: string
  amount?: string
  id?: string
}

const CardActions: React.FC<FarmCardActionsProps> = ({ contract, amount, id, account }) => {

  const renderApprovalOrStakeButton = () => {
    return false ? (
      // <StakeAction stakedBalance={null} tokenBalance={null} tokenName={lpName} pid={pid} depositFeeBP={depositFeeBP} />
      null
    ) : (
      <Button mt="8px" fullWidth disabled={false} onClick={() => purchasePay(contract, amount, id, account)}>
        Buy
      </Button>
    )
  }

  return (
    <Action>
      {renderApprovalOrStakeButton()}
    </Action>
  )
}

export default CardActions
