import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { provider } from 'web3-core'
import styled from 'styled-components'
import { Heading, Spinner } from '@pancakeswap-libs/uikit'
import FlexLayout from 'components/layout/Flex'
import Page from 'components/layout/Page'
import useRefresh from 'hooks/useRefresh'
import ItemCard from './components/ItemCard/ItemCard'
import { getItemsSellByItems } from '../../utils/callHelpers'
import { useNFT } from '../../hooks/useContract'

export interface FarmsProps{
  tokenMode?: boolean
}


const FlexLayoutSpinner = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-left: 220px;

  & > * {
    min-width: 280px;
    max-width: 24.5%;
    width: 100%;
    margin: 0 8px;
    margin-bottom: 32px;
  }

  @media (max-width: 768px) {
    margin-left: 220px;
  }
`

const Collection: React.FC<FarmsProps> = (farmsProps) => {

  const { account, ethereum }: { account: string; ethereum: provider } = useWallet()
  const contractNFT = useNFT();

  const [data, setData] = useState([]);

  const pathId = document.location.pathname.substring(12);

  const dispatch = useDispatch()
  const { fastRefresh } = useRefresh()
  useEffect(() => {
    async function getData(){
      const x = await getItemsSellByItems(contractNFT, pathId === 'null' ? account : pathId);
      setData(x)
    }
    getData()
  }, [account, dispatch, fastRefresh, contractNFT, pathId])


  return (
    <Page>
      <Heading as="h1" size="lg" color="primary" mb="50px" style={{ textAlign: 'center' }}>
        {data.length ? `All NFTs for sale by this artist` : `All NFTs for sale by this artist`}
      </Heading>

      <div>
        {/* <Divider /> */}
        <FlexLayout>
          {data.length ? 
            data.map(item => ( 
              <ItemCard
                key={item.id}
                ethereum={ethereum}
                account={account}
                item={item}
                contract={contractNFT}
              />
            ))
          : 
          <FlexLayoutSpinner>
            <Spinner />
          </FlexLayoutSpinner>
          }
         
        </FlexLayout>
      </div>
      {/* <Image src="/images/egg/mill.gif" alt="illustration" width={600} height={400} responsive /> */}
    </Page>
  )
}

export default Collection
